body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
}

b{
  display: inline;
}

p{
  font-size: 1.1rem;
}

/* Basic Css */

body {
  overflow-x: hidden;
}

.blue {
  background-color: #00308b;
}

.grey {
  background-color: #f2f2f2;
}

.text-white {
  color: white;
}

.block {
  display: block;
}

.row-reverse {
  flex-direction: row-reverse;
}

.mobile-br {
  display: none;
}

@media only screen and (max-width: 700px) {
  .desktop-br {
    display: none;
  }
  .mobile-br {
    display: block;
  }
  .center-text-mobile {
    text-align: center;
  }
}

/* type Css */

h1 {
  font-size: 30pt !important;
  font-weight: 550;
  line-height: 1.3;
}

.hero-h1 {
  font-size: 50px;
}

h2 {
  line-height: 1.4;
  font-size: 20px !important;
}

.body-text {
  color: white;
  font-size: 20px;
}

b {
  margin-bottom: 10px;
}

@media only screen and (max-width: 700px) {
  h1 {
    font-size: 1.7rem;
  }
  h2 {
    font-size: 1.6rem;
  }
  .hero-h1 {
    font-size: 30px;
    text-align: center;
  }
  b {
    font-size: 1.2rem;
  }
  .body-text {
    font-size: 17px;
  }
  .text {
    font-size: 17px;
    line-height: 1.7;
  }
}

/* Section Css */

.section-padding {
  padding: 150px 250px;
}

.section-padding-landing {
  padding: 0 250px;
}

.center-col {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 1600px) {
  .section-padding {
    padding: 125px 175px;
  }
  .section-padding-landing {
    padding: 0 175px;
  }
}

@media only screen and (max-width: 1440px) {
  .section-padding {
    padding: 100px 100px;
  }
  .section-padding-landing {
    padding: 0 100px;
  }
}

@media only screen and (max-width: 900px) {
  .section-padding {
    padding: 100px 50px;
  }
  .section-padding-landing {
    padding: 0 50px;
  }
}

@media only screen and (max-width: 700px) {
  .section-padding {
    padding: 50px 20px;
  }
  .section-padding-landing {
    padding: 0 25px;
  }
}

/* button css */

button:hover {
  opacity: 0.8;
  cursor: pointer;
}

.outline-button {
  border-style: solid;
  border-width: 2px;
}

.button-large {
  font-size: 22px;
  font-weight: 500;
  width: 200px;
  text-align: center;
  color: black;
  border-radius: 10px;
  outline: none;
  border: none;
  padding: 8px 20px;
  width: 100%;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
}

.green-button {
  background-color: #13d58a;
}

.green-button-outline {
  color: #13d58a;
}

.button-div {
  display: flex;
}

.button-center {
  display: block;
  margin: auto;
}

.button {
}

@media only screen and (max-width: 700px) {
  .button-div {
    display: block;
  }
  .button-large {
    width: 100%;
  }
}

/* Navbar Css */

.navbar {
  z-index: 1000;
  width: 100%;
}

.navbar-collapse {
  padding: 15px;
}

.nav-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
}

.nav-bar {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  padding-top: 40px;
}

.nav-left {
  display: flex;
  align-items: center;
}

.nav-bar-right {
  display: flex;
  align-items: center;
}

.nav-item {
  display: inline-block;
  padding-right: 40px;
}

.nav-item:last-of-type {
  padding-right: 0;
}

.nav-img-src {
  display: block;
}

.nav-item > a > p,
u {
  font-size: medium;
  color: white;
  font-weight: 500;
  margin: 0;
  padding: 0;
  padding-top: 2px;
  margin-left: 0;
}

@media only screen and (max-width: 1200px) {
  #basicExampleNav {
    background-color: #00308b;
  }
}

@media only screen and (max-width: 700px) {
  .nav-img-src {
    height: 30px;
    width: auto;
  }
}

/* landing page css */

.landing-page-container {
  display: flex;
  align-items: center;
  justify-content: left;
}

.landing-page {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
}

.landing-body {
  min-width: 160px;
  opacity: 0.8;
}

.hero-img-container {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  left: 50%;
  z-index: 0;
}

.one-ill {
  width: 80%;
  min-width: 400px;
}

.hero-img {
  padding-top: 50px;
}

.hero-img-mobile {
  display: none;
}

.hero-img-img {
  width: 750px;
}

.check-item {
  display: flex;
  color: white;
  font-size: 14px;
  align-items: center;
}

.check-item > p {
  margin-bottom: 0px;
}

.check-item > img {
  margin-right: 10px;
}

@media only screen and (max-width: 1440px) {
  .hero-img-img {
    width: 650px;
  }
  .hero-img-container {
    left: 55%;
  }
  .one-ill {
    max-width: 400px;
    min-width: 0;
  }
}

@media only screen and (max-width: 1100px) {
  .hero-img-img {
    display: none;
  }
}

@media only screen and (max-width: 700px) {
  .one-ill {
    max-width: 300px;
    min-width: 0;
  }
  .landing-page {
    min-height: 800px;
    height: 110vh;
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .landing-body {
    text-align: center;
  }
  .three-checks {
    display: none;
    margin: 0 auto;
    width: 100%;
    justify-content: center;
  }
  /* .hero-img-container-mobile {
      display: flex;
      width: 100%;
      justify-content: center;
  } */
  .hero-img-mobile {
    display: block;
    width: 90%;
    height: auto;
    margin: auto;
  }
}

